const iphoneToggle = document.querySelector('#alert-toggle')
const iphoneToggleWrapper = document.querySelector('.hero__img-graphic--toggle-btn')
const iphoneImgWrapper = document.querySelector('.hero__img')


iphoneToggle.addEventListener('click', ()=> {
	// console.log(iphoneToggle.checked)
	if(iphoneToggle.checked) {
iphoneImgWrapper.classList.add('toggle-active')
} else {
	iphoneImgWrapper.classList.remove('toggle-active')

	}
})




