	const watch = document.querySelector(".features-bottom__watch-img");

	window.addEventListener("scroll", () => {
			const rect = watch.getBoundingClientRect();
			const windowHeight = window.innerHeight;

			// Calculate the scroll progress
			let progress = (windowHeight - rect.top) / windowHeight;
			progress = Math.min(Math.max(progress, 0), 1); // Clamp between 0 and 1

			// Set scale from 1.4 to 2.2 and rotation from 0 to 30 degrees
			const scaleValue = 1.3 + (1.875 - 1.3) * progress;
			const rotateValue = 15 * progress;

			watch.style.transform = `scale(${scaleValue}) rotate(${rotateValue}deg)`;
	});


